@import "global.scss";

.section2_list-variety{
  @include list;
}

.section2_list-marker{
  position: absolute;
  left: -50px;
  top: 15px;
  color: $second-color;
  font-size: $descr-size;
  transition: all ease 250ms;
}

.section2_main-info{
  max-width: 530px;
  &>span{
    color: $second-color;
    font-size: 48px;
  }
}

.section2_title{
  font-size: 24px;
}

.section2_descr{
  font-size: $descr-size;
  line-height: 30px;
}

.section2_distance-descr{
  display: block;
  height: 15px;
}