//переменные

$second-color: #f88971; //цвет спанов
$descr-size: 18px; //размер шрифта для абзацов


//миксины

@mixin list{
  position: relative;
  font-size: $descr-size;
  &>li{
    margin: 15px 5px;
    cursor: pointer;
    color: #737373;
    &:hover{
      color: #000000;
    }
  }
}


*{
  box-sizing: border-box;
}

body{
  width: 100%;
  padding-left: 90px;
  padding-right: 90px;
  font-family: Roboto, Arial, sans-serif;
  background: #FBF5F4;
  color: #000000;
}

ul,li{
  list-style: none;
  margin: 0;
  padding: 0;
}

a{
  text-decoration: none;
}

button{
  background: none;
  border: none;
  cursor: pointer;
}

.section{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.small-logo{
  height: 30px;
  width: 48px;
}

.big-logo{
  max-height: inherit;
}

.send-btn{
  position: relative;
  background: $second-color;
  color: #ffffff;
  border-radius: 28px;
  font-size: 16px;
  padding: 21px 70px;
  transition: all ease 250ms;
  &:hover{
    -webkit-box-shadow: 0px 5px 17px -2px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 5px 17px -2px rgba(0,0,0,0.75);
    box-shadow: 0px 5px 17px -2px rgba(0,0,0,0.75);
  }
}

.section_btn-circle{
  position: absolute;
  left: 0;
  top: 0;
  width: 60px;
  height: 60px;
  background: #f87b4d;
  border-radius: 50%;
  &:before, &:after{
    content: '';
    position: absolute;
    top: 28px;
    width: 20px;
    height: 2px;
    background: #ffffff;
  }
  &:before{
    left: 20px;
  }
  &:after{
    left: 20px;
    transform: rotate(-90deg);
  }
}

.counter{
  display: flex;
  flex-direction: column;
  text-align: right;
}

.counter>span{
  color: $second-color;
  &:first-child{
    font-size: 72px;
  }
  &:last-child{
    font-size: 30px;
    padding-right: 10px;
  }
}